/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  filterType: 'SHOW_ALL',
  btnStatus: {
    isAllBtn: true,
    isActiveBtn: false,
    isDoneBtn: false,
  },
};

const filterSlice = createSlice({
  name: 'filter',
  initialState,
  reducers: {
    showAll(state) {
      state.filterType = 'SHOW_ALL';
      state.btnStatus = {
        isAllBtn: true,
        isActiveBtn: false,
        isDoneBtn: false,
      };
    },
    showActive(state) {
      state.filterType = 'SHOW_ACTIVE';
      state.btnStatus = {
        isAllBtn: false,
        isActiveBtn: true,
        isDoneBtn: false,
      };
    },
    showADone(state) {
      state.filterType = 'SHOW_DONE';
      state.btnStatus = {
        isAllBtn: false,
        isActiveBtn: false,
        isDoneBtn: true,
      };
    },
  },
});

export const { showAll, showActive, showADone } = filterSlice.actions;
export const selectTypeFilter = ({ filter }) => filter.filterType;
export const selectBtnSttus = ({ filter }) => filter.btnStatus;
export default filterSlice.reducer;
