/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  handledTodo: [],
};

const handlerSlice = createSlice({
  name: 'handler',
  initialState,
  reducers: {
    showAll(state, action) {
      state.handledTodo = action.payload;
    },
    showActive(state, action) {
      state.handledTodo = action.payload.filter((task) => task.isComplited === false);
    },
    showDone(state, action) {
      state.handledTodo = action.payload.filter((task) => task.isComplited === true);
    },
    searchText(state, action) {
      state.handledTodo = state.handledTodo.filter((task) => task.text.includes(action.payload));
    },
  },
});

export const {
  showAll, showActive, showDone, searchText,
} = handlerSlice.actions;
export const selectHandledTodo = ({ handler }) => handler.handledTodo;
export default handlerSlice.reducer;
