import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Title } from '../Title';
import { TaskAddition } from '../TaskAddition';
import { TaskListWrapper, SearchFilterWrapper } from './TaskList.styles';
import { Task } from '../Task';
import { TasksFilter } from '../TasksFilter';
import { TaskSearch } from '../TaskSearch';
import { selectTodo } from '../../store/reducers/todoSlice';
import {
  showAll, showActive, showDone, searchText, selectHandledTodo,
} from '../../store/reducers/handlerSlice';
import { selectTypeFilter } from '../../store/reducers/filterSlice';

export const TaskList = () => {
  const dispatch = useDispatch();
  const tasks = useSelector(selectTodo);
  const currentFilter = useSelector(selectTypeFilter);
  const searchedText = useSelector(({ search }) => search.text);

  const handledTasks = useSelector(selectHandledTodo);

  useEffect(() => {
    if (currentFilter === 'SHOW_ALL' && !searchedText) {
      dispatch(showAll(tasks));
    } else if (currentFilter === 'SHOW_ACTIVE' && !searchedText) {
      dispatch(showActive(tasks));
    } else if (currentFilter === 'SHOW_DONE' && !searchedText) {
      dispatch(showDone(tasks));
    } else if (searchedText) {
      dispatch(searchText(searchedText));
    }
  }, [tasks, currentFilter, searchedText]);

  return (
    <TaskListWrapper>
      <Title />
      <TaskAddition />
      {handledTasks.map((task) => (
        <Task
          key={task.id}
          id={task.id}
          isComplited={task.isComplited}
          text={task.text}
        />
      ))}
      <SearchFilterWrapper>
        <TaskSearch />
        <TasksFilter />
      </SearchFilterWrapper>
    </TaskListWrapper>
  );
};
